<template>
  <div class="edit-option-labels edit-action-part">
    <h3>左侧标题</h3>
    <ul class="option-info">
      <li v-for="(label, index) in labels" :key="index">
        <Row :gutter="20">
          <Col span="11">
          <Input v-model="label.content" @on-blur="changeLabels" placeholder="栏目" clearable></Input>
          </Col>
          <Col span="13">
          <ButtonGroup>
            <Button icon="plus" title="向下添加栏目" @click="addNewlabel(index)"></Button>
            <Button icon="minus" title="删除当前栏目" @click="delCurlabel(index)"></Button>
            <Button icon="arrow-up-c" title="上移一位" @click="changLabelUp(index)"></Button>
            <Button icon="arrow-down-c" title="下移一位" @click="changLabelDown(index)"></Button>
          </ButtonGroup>
          </Col>
        </Row>
      </li>
    </ul>
    <div class="option-add">
      <span @click="addNewlabel(-1)">
        <Icon type="plus"></Icon>添加栏目</span>
      <span @click="toBat">批量添加</span>
    </div>

    <transition name="fade">
      <modal-edit-bat-options v-if="modalIsShow" :option-cont="optionCont" @close="switchModal" @confirm="confirm"></modal-edit-bat-options>
    </transition>
  </div>
</template>

<script>
import deepmerge from "../../../libs/deepmerge";
import ModalEditBatOptions from "./ModalEditBatOptions.vue";
import { ranNum } from "../../../libs/utils";

export default {
  name: "edit-option-labels",
  props: {
    labelInfos: {
      type: Array
    }
  },
  watch: {
    labelInfos(val) {
      this.initData(val);
    }
  },
  components: {
    ModalEditBatOptions
  },
  data() {
    return {
      modalIsShow: false,
      optionCont: [],
      labels: []
    };
  },
  mounted() {
    this.initData(this.labelInfos);
  },
  methods: {
    initData(val) {
      this.labels = deepmerge(val, []);
    },
    changeLabels() {
      let infos = {
        labels: this.labels
      };
      this.$emit("change", { infos });
    },
    addNewlabel(index) {
      let i = this.labels.length + 1;
      let nindex = index === -1 ? i : index + 1;
      this.labels.splice(nindex, 0, {
        labelId: ranNum(),
        content: "栏目" + i
      });
      this.changeLabels();
    },
    delCurlabel(index) {
      this.labels.splice(index, 1);
      this.changeLabels();
    },
    changLabelUp(index) {
      if (index === 0) return;
      let label = this.labels.splice(index, 1);
      this.labels.splice(index - 1, 0, label[0]);
      this.changeLabels();
    },
    changLabelDown(index) {
      if (index === this.labels.length - 1) return;
      let label = this.labels.splice(index, 1);
      this.labels.splice(index + 1, 0, label[0]);
      this.changeLabels();
    },
    toBat() {
      let optionCont = [];
      this.labels.map(item => {
        optionCont.push(item.content);
      });
      this.optionCont = optionCont;
      this.switchModal();
    },
    switchModal() {
      this.modalIsShow = !this.modalIsShow;
    },
    confirm(selectCont) {
      let labels = [];
      selectCont.map((item, index) => {
        labels.push({
          labelId: ranNum(),
          content: item
        });
      });
      this.labels = labels;
      this.switchModal();
      this.changeLabels();
    }
  }
};
</script>



