import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

const state = {
  topics: [],
  curTopic: {}
};

const mutations = {
  setCurTopic(state, curTopic) {
    if (curTopic.id) {
      state.curTopic = Object.assign({}, state.curTopic, curTopic);
    } else {
      state.curTopic = {};
    }
  },
  setTopics(state, topics) {
    state.topics = topics;
  },
  modifyTopic(state, topic) {
    let tindex = -1;
    state.topics.map((item, index) => {
      if (item.id === topic.id) tindex = index;
    });
    if (tindex < 0) {
      state.topics.push(topic);
    } else {
      state.topics[tindex] = Object.assign(state.topics[tindex], topic);
    }
    state.curTopic = Object.assign({}, state.curTopic, topic);
  },
  afterAddTopic(state, { topic, index }) {
    state.topics.splice(index + 1, 0, topic);
    state.curTopic = Object.assign({}, state.curTopic, topic);
  },
  deleteTopic(state, index) {
    state.topics.splice(index, 1);
    if (!state.topics.length) state.curTopic = {};
  },
  moveUpTopic(state, index) {
    let topic = state.topics.splice(index, 1);
    state.topics.splice(index - 1, 0, topic[0]);
  },
  moveDownTopic(state, index) {
    let topic = state.topics.splice(index, 1);
    state.topics.splice(index + 1, 0, topic[0]);
  },
  clearTopics(state) {
    state.curTopic = {};
    state.topics = [];
  }
};


export default new Vuex.Store({
  state,
  mutations
});



