<template>
  <div class="page-edit-action">
    <edit-title v-if="hasCur" :titles="titles" @change="modTopic"></edit-title>

    <edit-options v-if="hasOption" :opt-infos="option" @change="modTopic"></edit-options>

    <edit-option-labels v-if="hasLabel" :label-infos="labels" @change="modTopic"></edit-option-labels>

    <edit-fill v-if="hasFill" :fill-cont="fills" @change="modTopic"></edit-fill>

    <Button v-if="hasRelated" @click="toSetRelated">关联逻辑</Button>

    <!-- edit related modal -->
    <modal-edit-related v-if="editRelatedIsShow" @close="switchModal" @confirm="setRelated"></modal-edit-related>
  </div>
</template>

<script>
import EditFill from "./snippet/EditFill.vue";
import EditTitle from "./snippet/EditTitle.vue";
import EditOptions from "./snippet/EditOptions.vue";
import EditOptionLabels from "./snippet/EditOptionLabels.vue";
import ModalEditRelated from "./snippet/ModalEditRelated.vue";
import store from "../../store";
import deepmerge from "@/libs/deepmerge";

export default {
  name: "page-edit-action",
  data() {
    return {
      titles: {},
      option: {},
      labels: [],
      fills: [],
      editRelatedIsShow: false
    };
  },
  components: {
    EditFill,
    EditTitle,
    EditOptions,
    EditOptionLabels,
    ModalEditRelated
  },
  computed: {
    topics() {
      return store.state.topics;
    },
    curTopic() {
      return store.state.curTopic;
    },
    hasCur() {
      return this.curTopic && this.curTopic.id;
    },
    hasFill() {
      return this.hasCur && this.curTopic.infos.fills.length;
    },
    hasOption() {
      return this.hasCur && this.curTopic.infos.options.length;
    },
    hasLabel() {
      return this.hasCur && this.curTopic.infos.labels.length;
    },
    hasRelated() {
      let type = this.curTopic.type;
      return this.hasCur && type !== "paging" && type !== "paragraph";
    }
  },
  watch: {
    curTopic(val) {
      this.initData(val);
    }
  },
  methods: {
    initData(val) {
      if (!val.id) return;
      this.titles = {
        type: val.type,
        subType: val.subType,
        title: val.title,
        subTitle: val.subTitle,
        isRequire: val.isRequire
      };
      this.option = {
        type: val.type,
        subType: val.subType,
        options: deepmerge([], val.infos.options),
        conf: { ...val.infos.conf }
      };
      this.labels = deepmerge([], val.infos.labels);
      this.fills = deepmerge([], val.infos.fills);
    },
    modTopic(topic) {
      // 填空题类型变换的处理
      if (topic.type && topic.type === "fill") {
        let fills = [];
        if (topic.subType === "multi" && !this.curTopic.infos.fills.length) {
          fills = [
            {
              type: "text",
              cont: "初始内容:"
            },
            {
              type: "fill",
              fillId: 0,
              cont: "_".repeat(8),
              len: 8
            }
          ];
        }
        topic["infos"] = { fills };
      }
      let newTopic = deepmerge(this.curTopic, topic, {
        arrayMerge: (destinationArray, sourceArray, options) => sourceArray
      });
      store.commit("modifyTopic", newTopic);
    },
    toSetRelated() {
      for (let i = 0, len = this.topics.length; i < len; i++) {
        if (["paging", "paragraph"].indexOf(this.topics[i].type) === -1) {
          if (this.curTopic.id !== this.topics[i].id) {
            this.switchModal();
          } else {
            this.$Message.error("第一题不能添加关联逻辑!");
          }
          return;
        }
      }
    },
    switchModal() {
      this.editRelatedIsShow = !this.editRelatedIsShow;
    },
    setRelated(related) {
      // console.log(related);
      this.modTopic({ related });
      this.switchModal();
    }
  }
};
</script>



