<template>
  <div class="page-edit survey-page">
    <div class="edit-header">
      <h1 class="page-name">问卷编辑</h1>
      <div class="page-actions">
        <Button type="primary" icon="ios-upload" @click="save">保存</Button>
        <Button type="primary" icon="eye" @click="toView">预览</Button>
      </div>
    </div>
    <page-edit-nav></page-edit-nav>

    <div class="edit-actions">
      <page-edit-action></page-edit-action>
    </div>
    <div class="edit-main">
      <div class="main-cont">
        <page-edit-content ref="PageEditContent" :page-data="pageData"></page-edit-content>
      </div>
    </div>
  </div>
</template>

<script>
import PageEditContent from "./PageEditContent.vue";
import PageEditAction from "./PageEditAction.vue";
import PageEditNav from "./PageEditNav.vue";
import store from "../../store";

export default {
  name: "page-edit",
  props: {
    pageData: {
      type: Object
    }
  },
  data() {
    return {};
  },
  components: {
    PageEditContent,
    PageEditAction,
    PageEditNav
  },
  methods: {
    save() {
      let editComp = this.$refs.PageEditContent;
      this.$emit("save", {
        title: editComp.title,
        description: editComp.description,
        topics: editComp.topics,
        toView: false
      });
    },
    toView() {
      let editComp = this.$refs.PageEditContent;
      this.$emit("save", {
        title: editComp.title,
        description: editComp.description,
        topics: editComp.topics,
        toView: true
      });
    }
  },
  beforeDestroy() {
    store.commit("clearTopics");
  }
};
</script>



