<template>
  <div class="page-edit-content">
    <div class="content-head" @click="switchEditTitle">
      <h1>{{title}}</h1>
      <p>{{description}}</p>
    </div>
    <div class="content-list">
      <ul>
        <span style="display: none">{{no = 0}}{{pageno = 1}}</span>
        <li v-for="(item, index) in topics" :key="index">
          <page-edit-topic :topic="item" :topic-index="nonTopic.indexOf(item.type) > -1 ? 1 : no++" :topic-list-index="index" :topic-page="item.type === 'paging' ? ++pageno : 0" @addTopicDown="addTopicDown"></page-edit-topic>
        </li>
      </ul>
    </div>

    <!-- create new topic modal -->
    <div class="new-topic" v-if="navIsShow" @click="navIsShow = false">
      <span class="new-tips"> &lt;--请在左侧菜单中选择要创建的题型</span>
      <page-edit-nav :after-topic-index="afterTopicIndex"></page-edit-nav>
    </div>
    <!-- edit topic title modal -->
    <page-edit-title :title="title" :description="description" @close="switchEditTitle" @confirm="confirmEditTitle" v-if="editTitleIsShow"></page-edit-title>

  </div>
</template>

<script>
import PageEditTopic from "./PageEditTopic.vue";
import PageEditNav from "./PageEditNav.vue";
import PageEditTitle from "./PageEditTitle.vue";
import store from "../../store";

export default {
  name: "page-edit-content",
  props: {
    pageData: {
      type: Object
    }
  },
  data() {
    return {
      title: "问卷名称",
      description: "这是一个很有意义的问卷，希望大家能认真填写。",
      afterTopicIndex: "",
      navIsShow: false,
      editTitleIsShow: false,
      nonTopic: ["paging", "paragraph"]
    };
  },
  components: {
    PageEditTopic,
    PageEditNav,
    PageEditTitle
  },
  computed: {
    topics() {
      return store.state.topics;
    }
  },
  mounted() {
    if (this.pageData) {
      this.getTopics(this.pageData);
    }
  },
  methods: {
    getTopics(storeTopics) {
      this.title = storeTopics.title;
      this.description = storeTopics.description;
      store.commit("setTopics", storeTopics.topics);
    },
    addTopicDown(index) {
      this.afterTopicIndex = index;
      this.navIsShow = true;
    },
    switchEditTitle() {
      this.editTitleIsShow = !this.editTitleIsShow;
    },
    confirmEditTitle({ title, description }) {
      this.title = title;
      this.description = description;
      this.switchEditTitle();
    }
  }
};
</script>



