<template>
  <modal class="page-edit-title" title="问卷信息编辑" @close="closeModal" @confirm="confirmModal">
    <ul class="title-main">
      <li>
        <span>问卷名称</span>
        <div class="title-cont">
          <Input v-model="tCont"></Input>
        </div>
      </li>
      <li>
        <span>问卷说明</span>
        <div class="title-cont">
          <Input type="textarea" v-model="dCont" :rows="8"></Input>
        </div>
      </li>
    </ul>
  </modal>
</template>

<script>
import Modal from "../common/Modal.vue";

export default {
  name: "page-edit-title",
  props: {
    title: String,
    description: String
  },
  components: {
    Modal
  },
  data() {
    return {
      tCont: this.title,
      dCont: this.description
    };
  },
  methods: {
    confirmModal() {
      if (!this.tCont) {
        this.$Message.error("请输入问卷名称");
        return;
      }
      this.$emit("confirm", {
        title: this.tCont,
        description: this.dCont
      });
    },
    closeModal() {
      this.$emit("close");
    }
  }
};
</script>



