<template>
  <modal class="modal-edit-text" :title="title" @close="closeModal" @confirm="confirmModal">
    <div class="text-main">
      <textarea class="text-cont" v-model="text" name="edit-text" rows="8" :placeholder="title"></textarea>
    </div>
  </modal>
</template>

<script>
import Modal from "../../common/Modal.vue";

export default {
  name: "modal-edit-text",
  props: {
    title: String,
    editText: String
  },
  data() {
    return {
      text: this.editText || ""
    };
  },
  components: {
    Modal
  },
  methods: {
    confirmModal() {
      this.$emit("confirm", this.text);
    },
    closeModal() {
      this.$emit("close");
    }
  }
};
</script>



