<template>
  <div class="edit-options edit-action-part">
    <h3>选项</h3>
    <ul>
      <li v-for="(opt, index) in options" :key="index">
        <Input v-model="opt.content" @on-blur="changeOptions" placeholder="选项" clearable></Input>
        <div class="option-info">
          <Row :gutter="20">
            <Col span="11">
            <InputNumber v-model="opt.score" :max="1000" :min="0" placeholder="分值" v-if="mainType === 'star'"></InputNumber>
            <Checkbox v-model="opt.isDefault" @on-change="setDefault(index)" v-if="mainType === 'select'">默认</Checkbox>
            <span :class="['option-tip', {'option-tip-act': opt.desc}]" @click="toEditText(index)">说明</span>
            </Col>
            <Col span="13">
            <ButtonGroup>
              <Button icon="plus" title="向下添加选项" @click="addNewOption(index)"></Button>
              <Button icon="minus" title="删除当前选项" @click="delCurOption(index)"></Button>
              <Button icon="arrow-up-c" title="上移一位" @click="changeOptionUp(index)"></Button>
              <Button icon="arrow-down-c" title="下移一位" @click="changeOptionDown(index)"></Button>
            </ButtonGroup>
            </Col>
          </Row>
        </div>
      </li>
    </ul>
    <div class="option-add">
      <span @click="addNewOption(-1)">
        <Icon type="plus"></Icon>添加选项</span>
      <span @click="toBat">批量添加</span>
    </div>
    <div class="option-set">
      <Row :gutter="10">
        <Col span="8" v-if="subType === 'multiple'">
        <Select v-model="conf.minSelect" @on-change="changeOptions" placeholder="至少选几项">
          <Option v-for="item in minCols" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        </Col>
        <Col span="8" v-if="subType === 'multiple'">
        <Select v-model="conf.maxSelect" @on-change="changeOptions" placeholder="最多选几项">
          <Option v-for="item in maxCols" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        </Col>
        <Col :span="subType === 'simple' ? 24 : 8">
        <Select v-model="conf.colNo" @on-change="changeOptions" placeholder="每行几列">
          <Option v-for="item in rowCols" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        </Col>
      </Row>
    </div>

    <!-- bat-options edit modal -->
    <modal-edit-bat-options v-if="batOptIsShow" :option-cont="optionCont" @close="switchBatOpt" @confirm="confirmBatOpt"></modal-edit-bat-options>
    <!-- text edit modal -->
    <modal-edit-text v-if="editTextIsShow" title="说明编辑" :edit-text="editText" @close="switchEditText" @confirm="confirmText"></modal-edit-text>

  </div>
</template>

<script>
import deepmerge from "../../../libs/deepmerge";
import ModalEditBatOptions from "./ModalEditBatOptions.vue";
import ModalEditText from "./ModalEditText.vue";
import { ranNum } from "../../../libs/utils";

let rowCols = [];
for (let i = 1; i <= 6; i++) {
  rowCols.push({
    value: i,
    label: "每行" + i + "列"
  });
}

export default {
  name: "edit-options",
  props: {
    optInfos: {
      type: Object
    }
  },
  watch: {
    optInfos(val) {
      this.initData(val);
    }
  },
  components: {
    ModalEditBatOptions,
    ModalEditText
  },
  data() {
    return {
      mainType: "",
      subType: "",
      options: [],
      score: 0,
      conf: {
        minSelect: 1,
        maxSelect: 999,
        colNo: 1
      },
      minCols: [],
      maxCols: [],
      rowCols,
      // edit-bat-options
      batOptIsShow: false,
      optionCont: [],
      // edit-text
      optInd: -1,
      editText: "",
      editTextIsShow: false
    };
  },
  mounted() {
    this.initData(this.optInfos);
    this.changeSelectCols();
  },
  methods: {
    initData(val) {
      this.mainType = val.type;
      this.subType = val.subType;
      this.conf = { ...val.conf };
      this.options = deepmerge(val.options, []);
    },
    changeSelectCols() {
      let len = this.options.length;
      let minCols = [];
      let maxCols = [];
      for (let i = 0; i < len; i++) {
        minCols.push({
          value: i + 1,
          label: "至少选" + (i + 1) + "项"
        });
        if (i > 0) {
          maxCols.push({
            value: i + 1,
            label: "最多选" + (i + 1) + "项"
          });
        }
      }
      this.minCols = minCols;
      this.maxCols = maxCols;
    },
    setDefault(pindex) {
      if (this.subType === "simple") {
        this.options.map((item, index) => {
          if (pindex !== index) item.isDefault = false;
        });
      }
    },
    changeOptions() {
      this.changeSelectCols();
      let infos = {
        conf: { ...this.conf },
        options: this.options
      };
      this.$emit("change", { infos });
    },
    addNewOption(pindex) {
      let i = this.options.length + 1;
      let index = pindex === -1 ? i - 1 : pindex + 1;
      this.options.splice(index, 0, {
        optionId: ranNum(),
        content: "选项内容" + i,
        desc: "",
        isDefault: false,
        sortNo: 0,
        score: this.mainType === "star" ? i : -1
      });

      this.changeOptions();
    },
    delCurOption(pindex) {
      this.options.splice(pindex, 1);
      this.changeOptions();
    },
    changeOptionUp(pindex) {
      if (pindex === 0) return;
      let option = this.options.splice(pindex, 1);
      this.options.splice(pindex - 1, 0, option[0]);
      this.changeOptions();
    },
    changeOptionDown(pindex) {
      if (pindex === this.options.length - 1) return;
      let option = this.options.splice(pindex, 1);
      this.options.splice(pindex + 1, 0, option[0]);
      this.changeOptions();
    },
    toBat() {
      let optionCont = [];
      this.options.map(item => {
        optionCont.push(item.content);
      });
      this.optionCont = optionCont;
      this.switchBatOpt();
    },
    switchBatOpt() {
      this.batOptIsShow = !this.batOptIsShow;
    },
    confirmBatOpt(selectCont) {
      let options = [];
      selectCont.map((item, index) => {
        options.push({
          optionId: ranNum(),
          content: item,
          desc: "",
          isDefault: false,
          sortNo: 0,
          score: this.mainType === "star" ? index + 1 : -1
        });
      });
      this.options = options;
      this.switchBatOpt();
      this.changeOptions();
    },
    toEditText(index) {
      this.optInd = index;
      this.editText = this.options[index].desc;
      this.switchEditText();
    },
    switchEditText() {
      this.editTextIsShow = !this.editTextIsShow;
    },
    confirmText(text) {
      this.options[this.optInd].desc = text;
      this.switchEditText();
      this.changeOptions();
    }
  }
};
</script>



