<template>
  <div class="page-view survey-page">
    <div class="view-pages" v-for="(list, index) in topics" :key="index" v-show="currentPage === index">
      <div class="view-head" v-if="index === 0">
        <h1>{{title}}</h1>
        <p>{{description}}</p>
      </div>
      <div class="view-topics">
        <ul>
          <li v-for="(item, tindex) in list" :key="tindex">
            <page-view-topic :topic="item" @finished="finished" v-if="topicsCtrl[item.id].show"></page-view-topic>
          </li>
          <li v-if="totalPage === index">
            <div class="view-confirm">
              <Button type="primary" long @click="submitHandle">提交</Button>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="view-info">
      <div class="view-info-btn">
        <Button size="small" type="primary" long @click="toPrev" :disabled="currentPage === 0">上一页</Button>
      </div>
      <div class="view-info-btn">第{{currentPage + 1}}页</div>
      <div class="view-info-btn">
        <Button size="small" type="primary" long @click="toNext" :disabled="currentPage === totalPage">下一页</Button>
      </div>
    </div>
  </div>
</template>

<script>
import PageViewTopic from "./PageViewTopic.vue";

export default {
  name: "page-view",
  props: {
    pageData: Object,
    needCheck: {
      type: Boolean,
      default: true
    }
  },
  components: {
    PageViewTopic
  },
  data() {
    return {
      title: "",
      description: "",
      topics: [],
      currentPage: 0,
      totalPage: 0,
      answers: {},
      topicsCtrl: {},
      unloadFunc: ""
    };
  },
  mounted() {
    this.getTopics(this.pageData);
    // this.addUnloadListen();
  },
  methods: {
    addUnloadListen() {
      let that = this;
      const unloadFunc = function(e) {
        that.$ls.set("answers", that.answers);
        let confirmationMessage = "当前有问卷在填写， 确定要离开吗？";
        (e || window.event).returnValue = confirmationMessage;
        return confirmationMessage;
      };
      this.unloadFunc = unloadFunc;
      window.addEventListener("beforeunload", unloadFunc);
    },
    getTopics(storeTopics) {
      let topics = [[]];
      let topicsCtrl = {};
      this.answers = storeTopics.answers;
      this.title = storeTopics.title;
      this.description = storeTopics.description;
      let topicNo = 0;
      storeTopics.topics.forEach(item => {
        let tlen = topics.length - 1;
        if (item.type !== "paging") {
          if (item.type !== "paragraph") topicNo++;
          item["topicNo"] = topicNo;
          // 反显answer
          item["answer"] =
            (this.answers[item.id] && this.answers[item.id].answer) || "";
          topics[tlen].push(item);
        } else {
          topics.push([item]);
        }
        // 只要该题设置了关联逻辑，默认先隐藏
        topicsCtrl[item.id] = {
          show: !!item["answer"] || !item.related.topicId,
          type: item.type,
          related: item.related
        };
      });
      this.topicsCtrl = topicsCtrl;
      this.topics = topics;
      this.totalPage = topics.length - 1;
    },
    checkCurpageIsFinished() {
      let finished = true;
      let unfinishTopicNo = 0;
      // 完成状态的优先级别：
      // 1: 不要求作答 => true
      // 2：要求作答，没显示 => true
      // 3：要求作答，已显示 => check
      this.topics[this.currentPage].forEach(item => {
        if (item.type !== "paragraph") {
          let tfinished = true;
          // 显示的必答题目才会进一步验证是否完成
          if (item.isRequire && this.topicsCtrl[item.id].show) {
            tfinished = this.answers[item.id] && this.answers[item.id].finished;
          }
          if (finished && !tfinished) unfinishTopicNo = item.topicNo;
          finished = finished && tfinished;
        }
      });
      return { finished, unfinishTopicNo };
    },
    toNext() {
      if (!this.needCheck) {
        this.currentPage++;
        return;
      }
      let { finished, unfinishTopicNo } = this.checkCurpageIsFinished();
      if (finished) {
        // this.createExportData();
        this.currentPage++;
      }
      this.$emit("next-page", {
        answers: this.answers,
        finished,
        unfinishTopicNo
      });
    },
    toPrev() {
      this.currentPage--;
    },
    finished({ topicId, answer, finished }) {
      this.answers[topicId] = {
        answer,
        finished
      };
      this.updateTopicCtrl({ topicId, answer });
      // console.log(topicId, answer, finished);
    },
    updateTopicCtrl({ topicId, answer }) {
      const canUpdate = ["select", "star"];
      if (canUpdate.indexOf(this.topicsCtrl[topicId].type) === -1) return;
      // 验证规则：所有选择/打分题中关联了当前作答题目的题目才会验证
      for (let id in this.topicsCtrl) {
        let rInfos = this.topicsCtrl[id].related;
        if (rInfos.topicId && rInfos.topicId === topicId) {
          let isContain = false;
          rInfos.relatedOptions.forEach(item => {
            isContain = isContain || answer.indexOf(item) > -1;
          });
          this.topicsCtrl[id].show = rInfos.relatedType
            ? isContain
            : !isContain;

          // 清除答案
          if (!this.topicsCtrl[id].show && this.answers[id]) {
            this.answers[id] = { answer: "", finished: false };
          }
        }
      }
    },
    createExportData() {
      let exportData = [];
      this.topics.forEach(itemGroup => {
        itemGroup.forEach(topic => {
          let ans = this.answers[topic.id] && this.answers[topic.id].answer;
          let { type } = topic;
          if (type !== "paging" && type !== "paragraph") {
            let edata = {
              topicId: topic.id,
              topicNo: topic.topicNo,
              title: topic.topicNo + "、" + topic.title,
              answer: ""
            };
            let answerArr = [];
            if (type === "select" || type === "star") {
              topic.infos.options.forEach(item => {
                if (ans && ans.indexOf(item.optionId) > -1) {
                  answerArr.push(item.content);
                }
              });
              edata.answer = answerArr.join(" | ");
              exportData.push(edata);
            } else if (type === "fill") {
              if (topic.subType === "simple") {
                edata.answer = (ans && ans[0]) || "";
                exportData.push(edata);
              } else {
                topic.infos.fills.forEach(fill => {
                  if (fill.type === "fill") {
                    let fillData = { ...edata };
                    fillData.answer = (ans && ans[fill.fillId]) || "";
                    exportData.push(fillData);
                  }
                });
              }
            } else if (type === "sort") {
              if (ans) {
                topic.infos.options.forEach(item => {
                  answerArr[ans[item.optionId] - 1] = item.content;
                });
                edata.answer = answerArr.join(" > ");
              }
              exportData.push(edata);
            } else if (type === "matrix") {
              topic.infos.labels.forEach((label, lind) => {
                let labelData = { ...edata };
                labelData.title =
                  "第" +
                  topic.topicNo +
                  "题：行" +
                  (lind + 1) +
                  "--" +
                  label.content;
                answerArr = [];

                if (ans) {
                  topic.infos.options.forEach(option => {
                    if (ans[label.labelId].indexOf(option.optionId) > -1) {
                      answerArr.push(option.content);
                    }
                  });
                }
                labelData.answer = answerArr.join(" | ");
                exportData.push(labelData);
              });
            }
          }
        });
      });
      return exportData;
    },
    submitHandle() {
      if (!this.needCheck) {
        this.$emit("finined", {
          answers: this.answers,
          exportData: "empty"
        });
        return;
      }
      let { finished, unfinishTopicNo } = this.checkCurpageIsFinished();
      if (finished) {
        // this.allFinished = this.currentPage === this.totalPage;
        if (this.currentPage !== this.totalPage) {
          this.$emit("unfinish");
          return;
        }
        this.$emit("finined", {
          answers: this.answers,
          exportData: this.createExportData()
        });
      }
      this.$emit("next-page", {
        answers: this.answers,
        finished,
        unfinishTopicNo
      });
    }
  }
  // beforeDestroy() {
  //   window.removeEventListener("beforeunload", this.unloadFunc);
  //   this.$ls.set("answers", this.answers);
  // }
};
</script>



