<template>
  <div class="edit-title edit-action-part">
    <h3>标题</h3>
    <ul v-if="type === 'paragraph' || type === 'paging'">
      <li>
        <Input v-model="title" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="标题" @on-blur="changeTopic" clearable></Input>
      </li>
    </ul>
    <ul v-else>
      <li>
        <Input v-model="title" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="标题" @on-blur="changeTopic"></Input>
      </li>
      <li v-if="typeList.length">
        <Select v-model="subType" placeholder="类型变换" @on-change="changeTopic">
          <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </li>
      <li>
        <Checkbox v-model="isRequire" @on-change="changeTopic">必答</Checkbox>
        <span class="to-sub-title" @click="subTitleIsShow = !subTitleIsShow">填写提示</span>
      </li>
      <li class="edit-sub-title" v-if="subTitleIsShow">
        <Input v-model="subTitle" type="textarea" :rows="2" @on-blur="changeTopic" placeholder="填写提示"></Input>
      </li>
    </ul>
  </div>
</template>

<script>
import subtypeList from "../../../data/subtype";

export default {
  name: "edit-title",
  props: {
    titles: {
      type: Object
    }
  },
  watch: {
    titles(val) {
      this.initData(val);
    }
  },
  data() {
    return {
      type: "",
      subType: "",
      title: "",
      subTitle: "",
      isRequire: "",
      subTitleIsShow: false,
      typeList: ""
    };
  },
  mounted() {
    this.initData(this.titles);
  },
  methods: {
    initData(val) {
      this.type = val.type;
      this.subType = val.subType;
      this.title = val.title;
      this.subTitle = val.subTitle;
      this.isRequire = val.isRequire;

      this.typeList = subtypeList[val.type];
    },
    changeTopic() {
      let data = {
        type: this.type,
        subType: this.subType,
        title: this.title,
        subTitle: this.subTitle,
        isRequire: this.isRequire
      };
      this.$emit("change", data);
    }
  }
};
</script>



