import { ranNum } from "../libs/utils";

export default {
  methods: {
    getTopicData(type) {
      let id = type + ranNum();
      let isSelect = ["simple", "multiple"].indexOf(type) > -1;
      let mainType = isSelect ? "select" : type;
      let subType = isSelect ? type : "simple";

      let data = {
        id,
        type: mainType,
        subType: subType,
        title: "标题",
        subTitle: "",
        isRequire: true,
        related: {}, // format to see Tips in the bottom
        infos: {
          options: [],
          conf: { colNo: 1, minSelect: 1, maxSelect: 999 },
          labels: [],
          fills: []
        }
      };

      if (
        mainType === "select" ||
        mainType === "sort" ||
        mainType === "matrix" ||
        mainType === "star"
      ) {
        let options = [];
        for (let i = 1; i <= 4; i++) {
          let id = ranNum();
          options.push({
            optionId: id,
            content: "选项内容" + id,
            desc: "",
            isDefault: false,
            sortNo: 0,
            score: mainType === "star" ? i : -1
          });
        }
        data.infos.options = options;
      }

      if (mainType === "matrix") {
        let labels = [];
        for (let i = 1; i <= 2; i++) {
          let id = ranNum();
          labels.push({
            labelId: id,
            content: "栏目" + i
          });
        }
        data.infos.labels = labels;
      }

      if (mainType === "paging") {
        data.title = "分页说明";
        data.isRequire = false;
      }

      if (mainType === "paragraph") {
        data.title = "段落说明";
        data.isRequire = false;
      }

      // console.log(data);
      return data;
    }
    // TODO
  }
};

// Tips:
// related:format =>
// {
//   topicId: String,
//   relatedType: Number, 1=>包含， 0=>不包含
//   relatedOptions: Array, [optionIndex, ...]
// }



