<template>
  <modal class="modal-edit-related" title="关联逻辑" @close="closeModal" @confirm="confirmModal">
    <div class="rel-main">
      <p class="rel-tips">提示：关联题目一旦被重新编辑，当前题目所做的关联逻辑很可能会失效。建议完成所有题目编辑之后，再做关联逻辑设置。</p>
      <ul class="rel-list">
        <li>
          <span>当前题目：</span>
          <p class="rel-cont">{{curTopic.title}}</p>
        </li>
        <li>
          <span>关联题目：</span>
          <p class="rel-cont">
            <Select v-model="selectTopicIndex" @on-change="selectTopic" placeholder="请选择要关联的题目">
              <Option v-for="item in topicList" :value="item.index" :key="item.topicId">{{ item.topicTitle }}</Option>
            </Select>
          </p>
        </li>
      </ul>
      <div class="rel-topic" v-if="options.length">
        <p class="topic-tips">
          <span>当关联题目</span>
          <span class="topic-tips-select">
            <Select size="small" v-model="relatedType">
              <Option v-for="(item, rindex) in relatedOptions" :key="rindex" :value="item.value">{{ item.label }}</Option>
            </Select>
          </span>
          <span>下面选项：</span>
        </p>
        <ul class="topic-options">
          <li v-for="(opt, pindex) in options" :key="pindex">
            <Checkbox v-model="opt.isSelect">
              {{opt.content}}
              <span class="score-tip" v-if="opt.score > 0">（分值：{{opt.score}} ）</span>
            </Checkbox>
          </li>
        </ul>
        <p class="topic-tips">
          <span>中的任意一个时，“当前题目”才出现</span>
        </p>
      </div>
    </div>
  </modal>
</template>

<script>
import Modal from "../../common/Modal.vue";
import store from "../../../store";

export default {
  name: "modal-edit-related",
  components: {
    Modal
  },
  data() {
    return {
      relatedType: 1,
      relatedOptions: [
        {
          value: 1,
          label: "选择"
        },
        {
          value: 0,
          label: "没有选择"
        }
      ],
      selectTopicIndex: -1,
      selectTopicId: "",
      options: [],
      topicList: []
    };
  },
  computed: {
    topics() {
      return store.state.topics;
    },
    curTopic() {
      return store.state.curTopic;
    }
  },
  mounted() {
    this.getTopicList();
    this.selectInfos();
  },
  methods: {
    getTopicList() {
      let isGetTopic = false;
      let topicList = [];
      let no = 0;
      let nonTopic = ["paging", "paragraph"];
      this.topics.map((item, index) => {
        isGetTopic = isGetTopic ? isGetTopic : item.id === this.curTopic.id;
        if (nonTopic.indexOf(item.type) === -1) ++no;
        if (item.type === "select" && !isGetTopic) {
          topicList.push({
            topicId: item.id,
            topicTitle: no + ". " + item.title,
            index
          });
        }
      });
      this.topicList = topicList;
    },
    selectInfos() {
      const that = this;
      let topicId = that.curTopic.related["topicId"];
      if (topicId) {
        that.topicList.forEach(item => {
          if (item.topicId === topicId) {
            that.selectTopicIndex = item.index;
            that.selectTopic(item.index);
          }
        });
      }
    },
    selectTopic(index) {
      let relatedOptions = this.curTopic.related["relatedOptions"];
      let options = this.topics[index].infos.options.map(opt => {
        return {
          id: opt.optionId,
          content: opt.content,
          score: opt.score,
          isSelect: relatedOptions && relatedOptions.indexOf(opt.optionId) > -1
        };
      });
      this.selectTopicId = this.topics[index].id;
      this.options = options;
    },
    closeModal() {
      this.$emit("close");
    },
    confirmModal() {
      let relatedOptions = [];
      this.options.map(item => {
        if (item.isSelect) relatedOptions.push(item.id);
      });
      let data = {
        topicId: this.selectTopicId,
        relatedType: this.relatedType,
        relatedOptions
      };
      this.$emit("confirm", data);
    }
  }
};
</script>



