<template>
  <div class="edit-fill edit-action-part">
    <h3>多项填空编辑</h3>
    <ul>
      <li>
        <Input v-model="fill" type="textarea" :autosize="{minRows: 5,maxRows: 10}" placeholder="标题" @on-blur="changeTopic" clearable></Input>
      </li>
      <li>
        <p style="color: #999;">提示：在需要天空的地方，请用下划线（“_”）表示</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "edit-fill",
  props: {
    fillCont: {
      type: Array
    }
  },
  data() {
    return {
      fill: ""
    };
  },
  watch: {
    fillCont(val) {
      this.initData(val);
    }
  },
  mounted() {
    this.initData(this.fillCont);
  },
  methods: {
    initData(val) {
      let fills = [];
      val.map(item => {
        if (item.type === "fill") {
          fills.push("_".repeat(item.len));
        } else {
          fills.push(item.cont);
        }
      });
      this.fill = fills.join("");
    },
    changeTopic() {
      let fills = [];
      let regex = /_+/g;
      let conts = this.fill.split(regex);
      let ulines = this.fill.match(regex);
      for (let i = 0, len = conts.length; i < len; i++) {
        if (conts[i]) {
          fills.push({
            type: "text",
            cont: conts[i]
          });
        }

        if (ulines[i]) {
          let len = ulines[i].length;
          fills.push({
            type: "fill",
            fillId: i,
            cont: "_".repeat(len),
            len
          });
        }
      }
      this.$emit("change", { infos: { fills } });
    }
  }
};
</script>



