<template>
  <modal class="modal-edit-bat-options" title="批量添加" @close="closeModal" @confirm="confirmModal">
    <div class="bat-main">
      <div class="bat-text">
        <h4 class="bat-title">选项内容</h4>
        <textarea class="bat-text-cont" v-model="selectCont" name="options" rows="8"></textarea>
      </div>
      <div class="bat-options">
        <h4 class="bat-title">预定义选项</h4>
        <ul>
          <li v-for="(opt, index) in options" :key="index" @click="selectOpt(index)"> {{opt.name}} </li>
        </ul>
      </div>
    </div>
  </modal>
</template>

<script>
import Modal from "../../common/Modal.vue";
import presetOptions from "../../../data/presetOptions";

export default {
  name: "modal-edit-bat-options",
  props: {
    optionCont: Array
  },
  components: {
    Modal
  },
  data() {
    return {
      options: presetOptions,
      selectIndex: 0,
      selectCont: ""
    };
  },
  mounted() {
    this.selectCont =
      this.optionCont && this.optionCont.length
        ? this.optionCont.join("\n")
        : presetOptions[0].list.join("\n");
  },
  methods: {
    selectOpt(index) {
      this.selectIndex = index;
      this.selectCont = presetOptions[index].list.join("\n");
    },
    confirmModal() {
      this.$emit("confirm", this.selectCont.split("\n"));
    },
    closeModal() {
      this.$emit("close");
    }
  }
};
</script>



