<template>
  <div class="page-edit-topic topic" :class="['topic-item-' + topic.type, {'topic-current': curTopic.id === topic.id}]" @click="actCurTopic">
    <div class="topic-header">
      <p class="topic-title" :class="{'topic-title-require': topic.isRequire}">
        <span class="topic-title-serial">{{topicIndex + 1}}. </span>
        <span>{{topic.title}}</span>
        <span class="topic-title-tip" v-if="topicSubTip">[{{topicSubTip}}]</span>
      </p>
      <p class="topic-title-sub">{{topic.subTitle}}</p>
    </div>
    <div class="paging-header" v-if="topicPage > 1">
      <div class="paging-split">
        <span class="paging-no">第{{topicPage}}页</span>
      </div>
      <p class="topic-title">
        <span>{{topic.title}}</span>
      </p>
    </div>

    <div class="topic-cont">
      <!-- 选择题 -->
      <div class="topic-cont-item" v-if="topicType === 1">
        <ul :class="['option-list', 'option-list-' + topic.infos.conf.colNo]">
          <li class="option-li" v-for="(opt, pindex) in topic.infos.options" :key="pindex">
            <p class="option-item">{{opt.content}}
              <span class="score-tip" v-if="opt.score > 0">（ {{opt.score}}分 ）</span>
            </p>
            <span class="option-tips" v-if="opt.desc">{{opt.desc}}</span>
          </li>
        </ul>
      </div>
      <!-- 填空题 -->
      <div class="topic-cont-item" v-else-if="topicType === 2">
        <div class="fill-area" v-if="topic.subType === 'simple'"></div>
        <div class="fill-multi" v-else>
          <span v-for="(fill, findex) in topic.infos.fills" :key="findex"> {{fill.cont}} </span>
        </div>
      </div>
      <!-- 矩阵题 -->
      <div class="topic-cont-item" v-else-if="topicType === 3">
        <table class="topic-table" cellspacing="0">
          <tr>
            <th></th>
            <th v-for="(opt, index) in topic.infos.options" :key="index">{{opt.content}}</th>
          </tr>
          <tr v-for="(label, lindex) in topic.infos.labels" :key="lindex">
            <td>{{label.content}}</td>
            <td v-for="(opt, index) in topic.infos.options" :key="index">
              <span class="td-checkbox"></span>
            </td>
          </tr>
        </table>
      </div>
      <!-- todo -->
    </div>

    <div class="topic-actions">
      <p class="topic-actions-left" @click="toAddNew">
        <Icon type="plus"></Icon> 在此题后插入新题</p>
      <ul @click.stop="">
        <li title="复制" @click="copy">
          <Button type="ghost" shape="circle" icon="ios-copy-outline"></Button>
        </li>
        <li title="删除" @click="del">
          <Button type="ghost" shape="circle" icon="ios-trash"></Button>
        </li>
        <li title="上移" @click="moveUp">
          <Button type="ghost" shape="circle" icon="arrow-up-c"></Button>
        </li>
        <li title="下移" @click="moveDown">
          <Button type="ghost" shape="circle" icon="arrow-down-c"></Button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import deepmerge from "../../libs/deepmerge";
import store from "../../store";

export default {
  name: "page-edit-topic",
  props: {
    topic: Object,
    topicIndex: {
      type: Number,
      default: 0
    },
    topicListIndex: {
      type: Number,
      default: 0
    },
    topicPage: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {};
  },
  computed: {
    topics() {
      return store.state.topics;
    },
    curTopic() {
      return store.state.curTopic;
    },
    topicType() {
      let type = this.topic.type;
      if (type === "select" || type === "sort" || type === "star") {
        return 1;
      } else if (type === "fill") {
        return 2;
      } else if (type === "matrix") {
        return 3;
      }
    },
    topicSubTip() {
      let subtip = "";
      if (this.topic.type === "sort") {
        subtip = "排序题";
      }

      if (this.topic.subType === "multiple") {
        subtip = "多选题";
      }
      return subtip;
    }
  },
  methods: {
    actCurTopic() {
      store.commit("setCurTopic", this.topic);
    },
    toAddNew() {
      this.$emit("addTopicDown", this.topicListIndex);
    },
    copy() {
      let topic = deepmerge(this.topic, {});
      let index = this.topicListIndex;
      topic.id = topic.type + Math.ceil(Math.random() * 100000);
      store.commit("afterAddTopic", { topic, index });
    },
    del() {
      store.commit("deleteTopic", this.topicListIndex);
    },
    moveUp() {
      if (this.topicListIndex === 0) return;
      store.commit("moveUpTopic", this.topicListIndex);
    },
    moveDown() {
      if (this.topicListIndex === this.topics.length - 1) return;
      store.commit("moveDownTopic", this.topicListIndex);
    }
  }
};
</script>



