const selects = [
  {
    value: "simple",
    label: "单选"
  },
  {
    value: "multiple",
    label: "多选"
  }
];

const subtype = {
  select: selects,
  fill: [
    {
      value: "simple",
      label: "单项"
    },
    {
      value: "multi",
      label: "多项"
    }
  ],
  sort: [],
  star: selects,
  matrix: selects,
  paging: [],
  paragraph: []
};

export default subtype;



