<template>
  <div class="page-edit-nav edit-navs">
    <ul>
      <li v-for="(nav, index) in navs" :key="index" @click="createTopic(nav.type)">
        <span>
          <Icon :type="nav.icon"></Icon>
        </span>
        <span>{{nav.name}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import pageMixin from "../../mixins/page";
import store from "../../store";

export default {
  name: "page-edit-nav",
  props: {
    afterTopicIndex: Number
  },
  mixins: [pageMixin],
  data() {
    return {
      navs: [
        {
          name: "单选题",
          type: "simple",
          icon: "android-radio-button-on"
        },
        {
          name: "多选题",
          type: "multiple",
          icon: "android-checkbox"
        },
        {
          name: "填空题",
          type: "fill",
          icon: "compose"
        },
        {
          name: "排序题",
          type: "sort",
          icon: "android-options"
        },
        {
          name: "打分题",
          type: "star",
          icon: "star"
        },
        {
          name: "矩阵",
          type: "matrix",
          icon: "grid"
        },
        {
          name: "段落说明",
          type: "paragraph",
          icon: "ios-list"
        },
        {
          name: "分页",
          type: "paging",
          icon: "ios-photos"
        }
      ]
    };
  },
  computed: {
    topics() {
      return store.state.topics;
    }
  },
  methods: {
    createTopic(type) {
      if (type === "paging" && !this.topics.length) {
        this.$Message.error("无法插入分页");
        return;
      }
      let topicData = this.getTopicData(type);
      if (typeof this.afterTopicIndex === "undefined") {
        store.commit("modifyTopic", topicData);
      } else {
        store.commit("afterAddTopic", {
          topic: topicData,
          index: this.afterTopicIndex
        });
      }
    }
  }
};
</script>



