<template>
  <div class="modal">
    <div class="modal-main">
      <div class="modal-head">
        <span class="modal-head-close" @click="close">×</span>
        <h1 class="modal-head-title">{{title}}</h1>
      </div>
      <div class="modal-body">
        <slot></slot>
      </div>
      <div class="modal-foot">
        <button class="modal-btn" @click="close">取消</button>
        <button class="modal-btn modal-btn-primary" @click="confirm">确认</button>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'modal',
  props: {
    title: String
  },
  methods: {
    close() {
      this.$emit('close');
    },
    confirm() {
      this.$emit('confirm');
    }
  }
};
</script>



