<template>
  <div class="page-view-topic topic" :class="['topic-item-' + topic.type]">
    <div class="topic-header">
      <p class="topic-title" :class="{'topic-title-require': topic.isRequire}">
        <span class="topic-title-serial">{{topic.topicNo}}. </span>
        <span>{{topic.title}}</span>
        <span class="topic-title-tip" v-if="topicSubTip">[{{topicSubTip}}]</span>
      </p>
      <p class="topic-title-sub" v-if="topic.subTitle">{{topic.subTitle}}</p>
    </div>

    <div class="topic-cont" v-if="dataReady">
      <!-- 选择题 -->
      <div class="topic-cont-item" v-if="topicType === 1">
        <CheckboxGroup size="large" v-model="selectedOpts" v-if="topic.subType === 'multiple'">
          <ul :class="['option-list', 'option-list-' + colNo]">
            <li class="option-li" v-for="(opt, pindex) in options" :key="pindex">
              <Checkbox :label="opt.optionId">
                {{opt.content}}
                <span class="score-tip" v-if="opt.score > 0">（ {{opt.score}}分 ）</span>
              </Checkbox>
              <span class="option-tips" v-if="opt.desc">{{opt.desc}}</span>
            </li>
          </ul>
        </CheckboxGroup>
        <RadioGroup size="large" v-model="selectedOpts" v-else>
          <ul :class="['option-list', 'option-list-' + colNo]">
            <li class="option-li" v-for="(opt, pindex) in options" :key="pindex">
              <Radio :label="opt.optionId">
                {{opt.content}}
                <span class="score-tip" v-if="opt.score > 0">（ {{opt.score}}分 ）</span>
              </Radio>
              <span class="option-tips" v-if="opt.desc">{{opt.desc}}</span>
            </li>
          </ul>
        </RadioGroup>
      </div>
      <!-- 填空题 -->
      <div class="topic-cont-item" v-else-if="topicType === 2">
        <Input type="textarea" :rows="3" v-model="fillCont" @on-blur="finishFill" placeholder="请输入内容" v-if="topic.subType === 'simple'"></Input>
        <div class="fill-multi" v-else>
          <span v-for="(fill, findex) in topic.infos.fills" :key="findex">
            <i v-if="fill.type === 'text'">{{fill.cont}}</i>
            <input type="text" v-model="fills[fill.fillId]" @change="finishFill" placeholder="请输入内容" :style="{width: 10 + fill.len * 10 + 'px'}" v-else></input>
          </span>
        </div>
      </div>
      <!-- 矩阵题 -->
      <div class="topic-cont-item" v-else-if="topicType === 3">
        <table class="topic-table" cellspacing="0">
          <tr>
            <th></th>
            <th v-for="(opt, index) in topic.infos.options" :key="index">{{opt.content}}</th>
          </tr>
          <tr v-for="(label, lindex) in topic.infos.labels" :key="lindex">
            <td>{{label.content}}</td>
            <td v-for="(opt, index) in topic.infos.options" :key="index">
              <Radio size="large" v-model="matrix[label.labelId][opt.optionId]" @click.prevent.native="selectMatrix(label.labelId, opt.optionId)" v-if="topic.subType === 'simple'"></Radio>
              <Checkbox size="large" v-model="matrix[label.labelId][opt.optionId]" @click.prevent.native="selectMatrix(label.labelId, opt.optionId)" v-else> </Checkbox>
            </td>
          </tr>
        </table>
      </div>
      <!-- 排序题 -->
      <div class="topic-cont-item" v-else-if="topicType === 4">
        <ul class="sort-list">
          <li v-for="(opt, pindex) in options" :key="pindex">
            <p @click="setSortNo(pindex)">
              <span :class="['sort-no', {'sort-no-act': opt.sortNo}]">
                <i v-if="opt.sortNo">{{opt.sortNo}}</i>
              </span>
              {{opt.content}}
            </p>
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "page-view-topic",
  props: {
    topic: Object
  },
  data() {
    return {
      colNo: 0,
      minSelect: 1,
      maxSelect: 1,
      selectedOpts: this.topic.subType === "simple" ? "" : [],
      options: [],
      fills: {},
      fillCont: "",
      matrix: {},
      dataReady: false
    };
  },
  computed: {
    topicType() {
      let type = this.topic.type;
      if (type === "select" || type === "star") {
        return 1;
      } else if (type === "fill") {
        return 2;
      } else if (type === "matrix") {
        return 3;
      } else if (type === "sort") {
        return 4;
      }
    },
    topicSubTip() {
      let subtip = "";
      if (this.topic.type === "sort") {
        subtip = "排序题";
      }

      if (this.topic.subType === "multiple") {
        subtip = "多选题";
      }
      return subtip;
    }
  },
  watch: {
    selectedOpts(val, oldval) {
      this.finishSelect();
    }
  },
  mounted() {
    this.initData();
  },
  methods: {
    initData() {
      this.colNo = this.topic.infos.conf.colNo;
      this.minSelect = this.topic.infos.conf.minSelect;
      this.maxSelect = this.topic.infos.conf.maxSelect;

      const { type, subType, answer } = this.topic;

      if (type === "select" || type === "sort" || type === "star") {
        this.options = this.topic.infos.options.map(item => {
          let nitem = { ...item };
          if (type === "sort") {
            nitem.sortNo = (answer && answer[item.optionId]) || item.sortNo;
          }
          return nitem;
        });
        // 答案复现
        if (type !== "sort") {
          this.selectedOpts = answer || this.selectedOpts;
        }
      }

      if (type === "fill") {
        if (subType === "simple") {
          this.fillCont = (answer && answer[0]) || "";
        } else {
          let fills = {};
          this.topic.infos.fills.forEach(item => {
            if (item.type === "fill") {
              fills[item.fillId] = (answer && answer[item.fillId]) || "";
            }
          });
          this.fills = fills;
        }
      }

      if (type === "matrix") {
        let matrix = {};
        this.topic.infos.labels.forEach(item => {
          matrix[item.labelId] = {};
          this.topic.infos.options.forEach(elem => {
            matrix[item.labelId][elem.optionId] =
              answer && answer[item.labelId].indexOf(elem.optionId) > -1;
          });
        });
        this.matrix = matrix;
      }
      this.dataReady = true;
    },
    setSortNo(pindex) {
      let no = 1;
      let sortNo = this.options[pindex].sortNo;
      if (sortNo) {
        this.options.forEach((item, index) => {
          if (item.sortNo > sortNo) {
            item.sortNo -= 1;
          }
          if (index === pindex) item.sortNo = 0;
        });
      } else {
        this.options.forEach(item => {
          if (item.sortNo) ++no;
        });
        this.options[pindex].sortNo = no;
      }
      this.finishSort();
    },
    selectMatrix(labelId, optionId) {
      if (this.topic.subType === "simple") {
        for (let key in this.matrix[labelId]) {
          this.matrix[labelId][key] = key === optionId;
        }
      } else {
        this.matrix[labelId][optionId] = !this.matrix[labelId][optionId];
      }
      this.finishMatrix();
    },
    finishSelect() {
      let topicId = this.topic.id;
      let answer = this.selectedOpts;

      let optsLen = this.selectedOpts.length;
      let finished =
        this.topic.subType === "simple"
          ? !!this.selectedOpts
          : optsLen >= this.minSelect && optsLen <= this.maxSelect;

      this.$emit("finished", { topicId, answer, finished });
    },
    finishFill() {
      let topicId = this.topic.id;
      let finished = true;
      let answer = [];

      if (this.topic.subType === "multi") {
        for (let k in this.fills) {
          finished = finished && !!this.fills[k];
          answer[parseInt(k)] = this.fills[k];
        }
      } else {
        answer.push(this.fillCont);
      }
      this.$emit("finished", { topicId, answer, finished });
    },
    finishSort() {
      let topicId = this.topic.id;
      let answer = {};
      let finished = true;

      this.options.forEach(item => {
        if (item.sortNo) answer[item.optionId] = item.sortNo;
        finished = finished && item.sortNo > 0;
      });
      this.$emit("finished", { topicId, answer, finished });
    },
    finishMatrix() {
      let topicId = this.topic.id;
      let answer = {};
      let finished = true;

      for (let lid in this.matrix) {
        let opts = [];
        for (let oid in this.matrix[lid]) {
          if (this.matrix[lid][oid]) opts.push(oid);
        }
        answer[lid] = opts.join();
        let optl = opts.length;
        finished =
          finished &&
          (this.topic.subType === "simple"
            ? optl > 0
            : optl >= this.minSelect && optl <= this.maxSelect);
      }
      this.$emit("finished", { topicId, answer, finished });
    }
  }
};
</script>



